@import "variables.scss";

body {
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: $site-background-1;

    &.no-scrolling {
        overflow: hidden;
    }

    overflow: auto;
    scrollbar-gutter: stable both-edges;
}

* {
    color: $text-color;
    font-family: $main-font-family;
    margin: 0;
    padding: 0;
    outline: none;
    scroll-behavior: smooth;
    z-index: 10;
}

p,
h1,
h2,
h3,
h4,
h5,
h6,
a {
    margin: 0;
    text-decoration: none;
}

p {
    font-family: $secondary-font-family;
    font-size: 14px;
}

.hero-font {
    font-size: 75px;
}

h1 {
    font-size: 50px;
}

h2 {
    font-size: 30px;
}

h3 {
    font-size: 26px;
}

@media all and (max-width: 1400px) {}

@keyframes fade-from-right-to-left {
    0% {
        right: -20px;
        opacity: 0;
    }

    100% {
        right: 0px;
        opacity: 1;
    }
}

@keyframes fade-from-left-to-right {
    0% {
        left: -20px;
        opacity: 0;
    }

    100% {
        left: 0px;
        opacity: 1;
    }
}

@keyframes fade-from-top-to-bottom {
    0% {
        top: -20px;
        opacity: 0;
    }

    100% {
        top: 0px;
        opacity: 1;
    }
}

@keyframes fade-from-bottom-to-top {
    0% {
        bottom: -20px;
        opacity: 0;
    }

    100% {
        bottom: 0px;
        opacity: 1;
    }
}

@keyframes resize-from-top-to-bottom {
    0% {
        height: 0%;
    }

    100% {
        height: 100%;
    }
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.App {
    position: relative;
    background: $main-linear-bg;
    overflow-x: hidden;
}

.particles {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 0;
    pointer-events: none;
}

::-webkit-scrollbar {
    position: absolute;
    height: 0;
    width: 5px;
    background: rgba(94, 94, 94, 0.541);
}

::-webkit-scrollbar-thumb {
    background: #fffeef76;
    -webkit-border-radius: 1ex;
    -webkit-box-shadow: 0px 1px 2px rgba(0, 0, 0, 0);

    transition: all 0.5s;

    &:hover {
        background: #fffeefcd;
    }
}

::-webkit-scrollbar-corner {
    background: rgb(0, 0, 0);
}